* {
    box-sizing: border-box;
}

.App {

    font-family: Arial, Helvetica, sans-serif;
    max-width: 1920px;
    margin: 0 auto;
    padding-left: 8.5%;
    padding-right: 5%;

}

.pics {
    margin-top: 50px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    text-align: center;

}



.pic {
    margin: 0;
    border-radius: 5px;
    padding: 0;
    margin: 5px;
    width: 47%;
    flex-basis: width;
    position: relative;
}



.picsize {
    width: 80%;
    transition: 0.5s;

}

.picsize:hover {
    transform: scale(1.05);
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 15px;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}