.BigForm {
    height: 100vh;
    width: 200vh;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
}

.BigForm.active {
    opacity: 1;
    pointer-events: all;
}

.BigForm_content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    height: 90%;
    width: 90%;
    transform: scale(0.7);
    transition: 0.4s all;
}

.BigForm_content.active {
    transform: scale(1);
}